import React from "react";
import { Col, Button } from "react-bootstrap";
import { useEffect, useState } from "react";
import jsPDFInvoiceTemplate, {
  OutputType,
  jsPDF,
} from "jspdf-invoice-template-nodejs";

const GrandTotal = ({ reducerValue }) => {
  const [grandPrice, setGrandPrice] = useState(0);
  const [items, setItems] = useState({});
  const [count, setCount] = useState(0);

  useEffect(() => {
    const processor = JSON.parse(localStorage.getItem("processor"));
    const motherboard = JSON.parse(localStorage.getItem("motherboard"));
    const ram = JSON.parse(localStorage.getItem("ram"));
    const storage = JSON.parse(localStorage.getItem("storage"));
    const cassing = JSON.parse(localStorage.getItem("cassing"));
    const vga = JSON.parse(localStorage.getItem("vga"));
    const psu = JSON.parse(sessionStorage.getItem("psu"));
    const monitor = JSON.parse(localStorage.getItem("monitor"));
    const keyboard = JSON.parse(localStorage.getItem("keyboard"));
    const mouse = JSON.parse(localStorage.getItem("mouse"));
    const headset = JSON.parse(localStorage.getItem("headset"));
    const speaker = JSON.parse(localStorage.getItem("speaker"));
    const gamingChair = JSON.parse(localStorage.getItem("gamingChair"));
    const gamingDesk = JSON.parse(localStorage.getItem("gamingDesk"));
    const mousepad = JSON.parse(localStorage.getItem("mousepad"));

    let data = [];
    if (processor !== null && processor !== 0) {
      data.push(processor);
    }
    if (motherboard !== null && motherboard !== 0) {
      data.push(motherboard);
    }
    if (ram !== null && ram !== 0) {
      data.push(ram);
    }
    if (storage !== null && storage !== 0) {
      data.push(storage);
    }
    if (cassing !== null && cassing !== 0) {
      data.push(cassing);
    }
    if (vga !== null && vga !== 0) {
      data.push(vga);
    }
    if (psu !== null && psu !== 0) {
      data.push(psu);
    }
    if (monitor !== null && monitor !== 0) {
      data.push(monitor);
    }
    if (keyboard !== null && keyboard !== 0) {
      data.push(keyboard);
    }
    if (mouse !== null && mouse !== 0) {
      data.push(mouse);
    }
    if (headset !== null && headset !== 0) {
      data.push(headset);
    }
    if (speaker !== null && speaker !== 0) {
      data.push(speaker);
    }
    if (gamingChair !== null && gamingChair !== 0) {
      data.push(gamingChair);
    }
    if (gamingDesk !== null && gamingDesk !== 0) {
      data.push(gamingDesk);
    }
    if (mousepad !== null && mousepad !== 0) {
      data.push(mousepad);
    }

    setItems(data);
  }, [reducerValue]);

  useEffect(() => {
    // console.log(items);
    setCount(count + 1);
    if (count >= 3) {
      let total = 0;
      items.map((item) => {
        const price = Number(item.price);
        total += price;
      });
      setGrandPrice(total);
    }
    console.log(items);
  }, [items]);

  const generatePdf = () => {
    var props = {
      outputType: OutputType.Save,
      returnJsPDFDocObject: true,
      fileName: "Invoice Rakit PC",
      orientationLandscape: false,
      compress: true,
      logo: {
        src: "",
        type: "PNG", //optional, when src= data:uri (nodejs case)
        width: 53.33, //aspect ratio = width/height
        height: 26.66,
        margin: {
          top: 0, //negative or positive num, from the current position
          left: 0, //negative or positive num, from the current position
        },
      },
      stamp: {
        inAllPages: true, //by default = false, just in the last page
        src: "https://raw.githubusercontent.com/edisonneza/jspdf-invoice-template/demo/images/qr_code.jpg",
        type: "JPG", //optional, when src= data:uri (nodejs case)
        width: 20, //aspect ratio = width/height
        height: 20,
        margin: {
          top: 0, //negative or positive num, from the current position
          left: 0, //negative or positive num, from the current position
        },
      },
      business: {
        name: "Planet Computer",
        address: "Jln. Perintis Kemerdekaan no.23",
        phone: "0822 8866 6688",
        email: "planetcomputer@gmail.com",
        website: "planetcomputer.com",
      },
      contact: {
        label: "Invoice issued for:",
        name: "Client Name",
        address: "Albania, Tirane, Astir",
        phone: "(+355) 069 22 22 222",
        email: "client@website.al",
        otherInfo: "www.website.al",
      },
      invoice: {
        label: "Invoice #: ",
        num: 19,
        invDate: "Payment Date: 01/01/2021 18:12",
        invGenDate: "Invoice Date: 02/02/2021 10:17",
        headerBorder: false,
        tableBodyBorder: false,

        header: [
          {
            title: "No.",
            style: {
              width: 10,
            },
          },
          {
            title: "Nama Barang",
            style: {
              width: 80,
            },
          },
          {
            title: "Kategori",
          },
          { title: "Harga" },
        ],

        table: Array.from(items, (item, index) => [
          index + 1,
          item.name,
          item.kategori,
          item.price,
        ]),
        additionalRows: [
          {
            col1: "Total:",
            col2: "Rp. " + String(numberWithCommas(grandPrice)),
            style: {
              fontSize: 14, //optional, default 12
            },
          },
          {
            col1: "Discount:",
            col2: "0%",
            style: {
              fontSize: 10, //optional, default 12
            },
          },
          {
            col1: "SubTotal:",
            col2: "Rp. " + String(numberWithCommas(grandPrice)),
            style: {
              fontSize: 10, //optional, default 12
            },
          },
        ],
        invDescLabel: "Invoice Note",
        invDesc:
          "There are many variations of passages of Lorem Ipsum available, but the majority have suffered alteration in some form, by injected humour, or randomised words which don't look even slightly believable. If you are going to use a passage of Lorem Ipsum, you need to be sure there isn't anything embarrassing hidden in the middle of text. All the Lorem Ipsum generators on the Internet tend to repeat predefined chunks as necessary.",
      },
      footer: {
        text: "The invoice is created on a computer and is valid without the signature and stamp.",
      },
      pageEnable: true,
      pageLabel: "Page ",
    };
    const pdfObject = jsPDFInvoiceTemplate(props);
    pdfObject.jsPDFDocObject.addPage();
    console.log("pdf dihasilkan");
  };
  return (
    <Col className=" bg-dark " xl={{ span: 10, offset: 2 }}>
      <div className="komponen p-2">
        <div className=" text-white fw-bold offset-md-8 offset-1">
          <label htmlFor="total">Grand Total &nbsp;&nbsp;</label>
          <input
            type="text"
            disabled
            className="bg-white fw-bold"
            value={"Rp. " + numberWithCommas(grandPrice)}
            id="total"
          />
          <Button variant="success" className=" ms-2" onClick={generatePdf}>
            Save
          </Button>
        </div>
      </div>
    </Col>
  );
};

export default GrandTotal;
export const numberWithCommas = (x) => {
  return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
};
